.custom-radio .radio-label {
  position: relative;
  user-select: none;
  display: inline-block;
  margin-right: 28px;
}
.custom-radio .radio-label input {
  opacity: 0;
  position: absolute;
  cursor: pointer;
}
.custom-radio .radio-label input:checked ~ .radio-mark {
  border-color: #f9b851;
}
.custom-radio .radio-label input:checked ~ .radio-mark:before {
  opacity: 1;
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}
.custom-radio .radio-label .radio-mark {
  position: absolute;
  top: -3px;
  left: 0;
  width: 18px;
  height: 18px;
  cursor: pointer;
  border-width: 2px;
  border-style: solid;
  border-color: #e4e4e4;
  border-image: initial;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.custom-radio .radio-label .radio-mark:before {
  top: 2px;
  left: 2px;
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
  background-color: #f9b851;
  opacity: 0;
  -webkit-transform: scale(0.1);
  -moz-transform: scale(0.1);
  -ms-transform: scale(0.1);
  -o-transform: scale(0.1);
  transform: scale(0.1);
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}

