/*==== menu-toggle ====*/
.menu-toggle {
  width: 50px;
  height: 42px;
  position: relative;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  background-color: rgba(255, 255, 255, 0.1);
  cursor: pointer;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 4px;
  display: none;
}
@media (max-width: 1199px) {
  .menu-toggle {
    display: block;
  }
}
.menu-toggle .menu__bar {
  display: block;
  height: 2px;
  position: absolute;
  left: 10px;
  width: calc(100% - 20px);
  background: #fff;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.menu-toggle .menu__bar:nth-child(1) {
  top: 10px;
}
.menu-toggle .menu__bar:nth-child(2) {
  top: 20px;
}
.menu-toggle .menu__bar:nth-child(3) {
  top: 30px;
}
.menu-toggle.active .menu__bar:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 20px;
}
.menu-toggle.active .menu__bar:nth-child(2) {
  opacity: 0;
  visibility: hidden;
}
.menu-toggle.active .menu__bar:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  top: 20px;
}

.menu-toggle-black {
  background-color: rgba(128, 137, 150, 0.1);
}
.menu-toggle-black .menu__bar {
  background: #808996;
}

.drop-menu-toggle {
  font-size: 20px;
