/* info-box */
.info-box {
  background-color: #fff;
  padding: 40px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  -webkit-box-shadow: 0 10px 40px rgba(82, 85, 90, 0.1);
  -moz-box-shadow: 0 10px 40px rgba(82, 85, 90, 0.1);
  box-shadow: 0 10px 40px rgba(82, 85, 90, 0.1);
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  margin-bottom: 30px;
  position: relative;
  text-align: center;
  z-index: 1;
}
.info-box .info-icon {
  width: 85px;
  height: 85px;
  line-height: 85px;
  -webkit-border-radius: 59% 41% 60% 40%/46% 66% 34% 54%;
  -moz-border-radius: 59% 41% 60% 40%/46% 66% 34% 54%;
  border-radius: 59% 41% 60% 40%/46% 66% 34% 54%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  background-color: #7b68ee;
  color: #fff;
  text-align: center;
  font-size: 36px;
  -webkit-box-shadow: 0 5px 25px rgba(123, 104, 238, 0.3);
  -moz-box-shadow: 0 5px 25px rgba(123, 104, 238, 0.3);
  box-shadow: 0 5px 25px rgba(123, 104, 238, 0.3);
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.info-box .info-number {
  position: absolute;
  left: 10px;
  top: 10px;
  width: 33px;
  height: 33px;
  line-height: 30px;
  color: #7b68ee;
  background-color: rgba(123, 104, 238, 0.1);
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  font-weight: 500;
  font-size: 16px;
}
.info-box .info__title {
  font-size: 20px;
  color: #333f57;
  font-weight: 600;
  margin-bottom: 20px;
}
.info-box .info__desc {
  font-size: 16px;
  color: #808996;
  font-weight: 500;
}
.info-box:hover {
  -webkit-transform: translateY(-4px);
  -moz-transform: translateY(-4px);
  -ms-transform: translateY(-4px);
  -o-transform: translateY(-4px);
  transform: translateY(-4px);
}
.info-box:hover .info-icon,
.info-box:hover .info-number {
  background-color: #533ae9;
}
.info-box:hover .info-number {
  color: #fff;
}

