/* map-info-popup */
.map-info-popup {
  display: block;
  position: relative;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
}
.map-info-popup:before {
  content: "";
  height: 20px;
  width: 20px;
  background-color: #fff;
  position: absolute;
  bottom: -10px;
  left: 50%;
  -webkit-transform: translateX(-50%) rotate(45deg);
  -moz-transform: translateX(-50%) rotate(45deg);
  -ms-transform: translateX(-50%) rotate(45deg);
  -o-transform: translateX(-50%) rotate(45deg);
  transform: translateX(-50%) rotate(45deg);
}

/* item-popup-box */
.item-popup-box {
  display: block;
  width: 100%;
  position: relative;
  overflow: hidden;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  background-color: #fff;
  -webkit-box-shadow: 0 0px 9px rgba(0, 0, 0, 0.01);
  -moz-box-shadow: 0 0px 9px rgba(0, 0, 0, 0.01);
  box-shadow: 0 0px 9px rgba(0, 0, 0, 0.01);
}
.item-popup-box img {
  width: 100%;
  height: auto;
  -webkit-border-radius: 8px 8px 14px 14px;
  -moz-border-radius: 8px 8px 14px 14px;
  border-radius: 8px 8px 14px 14px;
}
.item-popup-box .close-info {
  position: absolute;
  width: 30px;
  height: 30px;
  line-height: 31px;
  top: 20px;
  text-align: center;
  right: 20px;
  color: #fff;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  z-index: 20;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.1);
  font-size: 16px;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
}
.item-popup-box .close-info .la-close {
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  -ms-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  font-weight: 700;
}
.item-popup-box .close-info:hover {
  background-color: #fff;
}
.item-popup-box .close-info:hover .la-close {
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
  color: #7b68ee;
